*{
    box-sizing:border-box;
}



body{
    margin: 0;
    padding : 0;
    font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",RobUbuntu,Cantarell
        ,"Open Sans","Helvetica Neue",sans-serif;
    background-color:#eff3f7;
   height:100%;
}

html, body, #potato, container {
    height: 100%;
    display:flex;
    justify-content:center;
}
.loader{
    width:100%;
    height:100%;
    display:flex;
    justify-content:center;
    align-items:center;

}

.movies{
    display:flex;
    justify-content:space-between;
    align-items:flex-start;
    flex-wrap:wrap;
    padding:50px;
    padding-top:70px;
    width: 80%;

}

    .movies .movie {
        width: 45%;
        background-color: white;
        margin-bottom: 70px;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        font-weight: 300;
        padding: 20px;
        border-radius: 5px;
        color: #B2B3BC;
        box-shadow: 0 13px 27px -5px;
        
    }

.movie img {
    position: relative;
    top: -50px;
    max-width: 120px;
    margin-right: 30px;
    color: #B2B3BC;
    box-shadow:0 13px 27px -5px;
}

.movie movie_title,
.movie movie_year {
    margin: 0;
    font-weight: 300;
}

.movie .movie_title{
    margin-bottom:5px;
    font-size:22px;
    color:#2c2c2c;
}

.movie .movie_genres {
    color:#B2B3BC;
    list-style:none;
    padding:0;
    margin :0;
    display:flex;
    margin:5px;
}

.movie_genres li{
    margin-left:10px;
    font-size:14px;
}